import Foot from '~/components/Foot'
import Head from '~/components/Head'
import { Button, List, InputItem, Toast, WingBlank, WhiteSpace } from 'antd-mobile'
import { failToast, loadingToast, successToast } from '&/methods'
import { axios } from '&/axios'
import { history } from '&/history'
export default class AdminMine extends Component {
  constructor() {
    super()
    this.state = {
      adminpwd: ''
    }
  }


  changepwd=()=>{
    var pwd = this.adminpwd.state.value;
    if(pwd.length==0){
      failToast('请输入密码',0.8);
      return;
    }
    axios.post('/bookstore/changeAdminPwd',{userpwd:pwd}).then(res=>{
      this.setState({
        adminpwd:''
      })
    })
  }

  exit=()=>{
    loadingToast('正在退出',0.5);
    setTimeout(() => {
      history.push('/login')
    }, 1000);
    
  }

  render() {
    return (
      <div style={{height:'100vh', backgroundColor:'#fff'}}>
        <Head title='密码修改'></Head>
        <div style={{position:'relative'}}>
          <List>
            <InputItem
              clear
              type='password'
              placeholder="请输入密码"
              ref={el => this.adminpwd = el}
              onChange={v => { this.setState({ adminpwd: v }) }}
              value={this.state.adminpwd}
            >密码</InputItem>
          </List>
          <div style={{ display: 'flex', justifyContent: 'space-around', alignItems: 'center' ,marginTop:'30px'}}>
            <Button type='primary' style={{ width: '40%' }} onClick={this.changepwd}>保存</Button>
            <Button type='warning' style={{ width: '40%' }} onClick={this.exit}>退出</Button>
          </div>
        </div>
        <Foot></Foot>
      </div>
    )
  }
}